.btn {
  display: inline-block;
  height: 38px;
  line-height: 38px;
  padding: 0 25px;
  background: #fff;
  border: none;
  outline: none;
  box-shadow: none;
  font-size: 16px;
  color: #aaa;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.24);
}
.btn.btn-icon {
  position: relative;
}
.btn.btn-icon .text,
.btn.btn-icon .icon {
  display: inline-block;
  height: 100%;
  text-align: center;
}
.btn.btn-icon .text {
  position: relative;
  padding-right: 37px;
  line-height: inherit;
  text-transform: uppercase;
  left: 0;
  -webkit-transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.btn.btn-icon .text:after {
  content: '';
  background-color: rgba(0, 0, 0, 0.4);
  width: 2px;
  height: 60%;
  position: absolute;
  top: 20%;
  right: 25px;
  box-shadow: 0px 1px 0 rgba(255, 255, 255, 0.25), 0px -1px 0 rgba(0, 0, 0, 0.35);
  background-color: #cccccc;
}
.btn.btn-icon .icon {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 25px;
  text-align: center;
  -webkit-transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.btn.btn-icon .icon .fa {
  height: 20px;
  vertical-align: middle;
  transition: height 0.1s ease, font-size 0.1s ease;
}
.btn.btn-icon .icon .fa.success,
.btn.btn-icon .icon .fa.error {
  display: none;
}
.btn.btn-icon.success .text,
.btn.btn-icon.error .text,
.btn.btn-icon:hover .text {
  transform: translateX(-100%);
  opacity: 0;
}
.btn.btn-icon.success .icon,
.btn.btn-icon.error .icon,
.btn.btn-icon:hover .icon {
  right: 50%;
  transform: translateX(50%);
}
.btn.btn-icon.success .icon .fa,
