  width: 100%;
  margin-bottom: 20px;
  background: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.24);
}
.todo-box {
  position: relative;
  overflow: hidden;
  display: inline-block;
  width: 100%;
  margin-bottom: 20px;
  background: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.24);
}
.todo-box .todo-title {
  margin: 15px 20px 10px;
  font-size: 16px;
  font-weight: 300;
  color: #888;
  outline: none;
  text-align: center;
}
.todo-box .todo-tools {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 8px;
  color: #999;
  opacity: 0;
  -webkit-transition: 0.1s opacity ease;
  transition: 0.1s opacity ease;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.todo-box .todo-tools .tool {
  flex: 0 auto;
  display: block;
  padding: 4px;
  font-size: 18px;
  cursor: pointer;
  color: #999;
  -webkit-transition: all 0.1s ease all ease;
  transition: all 0.1s ease all ease;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.todo-box .todo-tools .tool.clear:hover {
  color: #f44336;
}
.todo-box .todo-tools .tool.remove:hover {
  color: #455a64;
}
.todo-box .todo-tools .mover {
  padding: 0;
  display: flex;
}
.todo-box .todo-tools .mover .tool:hover {
  color: #2196f3;
}
.todo-box .todo-list {
  display: block;
  background: #fff;
}
.todo-box:hover .todo-tools {
  opacity: 1;
}
.todo-item,
.todo-add {
  display: flex;
  align-items: flex-start;
  border-top: 1px solid #eee;
}
.todo-item--complete,
.todo-add--complete {
  flex: 0 auto;
  order: 1;
  display: block;
  width: 36px;
  height: 36px;
  margin: 0;
  padding-top: 10px;
  text-align: center;
}
.todo-item--text,
.todo-add--text,
.todo-item--text .add-box,
.todo-add--text .add-box {
  flex: 1 0;
  order: 2;
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 10px 0;
  font-family: 'Helvetica';
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.03em;
  outline: none;
  cursor: text;
}
.todo-item .add-box,
.todo-add .add-box {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.todo-item .placeholder,
.todo-add .placeholder {
  color: #888;
}
.todo-item--icon,
.todo-add--icon {
  flex: 0 auto;
  display: block;
  width: 36px;
  height: 36px;
  margin: 0;
  text-align: center;
  color: #999;
  cursor: pointer;
}
.todo-item--icon.add,
.todo-add--icon.add {
  order: 1;
  font-size: 18px;
  padding: 9px;
}
.todo-item--icon.remove,
.todo-add--icon.remove {
  order: 3;
  font-size: 16px;
}
.todo-item:first-child,
.todo-add:first-child {
  border-top: none;
}
.todo-item.editable.active,
.todo-add.editable.active {
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 15px;
  padding-right: 15px;
  box-shadow: inset 0 1px 2px 1px rgba(0, 0, 0, 0.18);
  border-top-color: transparent;
}
.todo-item.editable.active + .todo-item,
.todo-add.editable.active + .todo-item {
  border-top-color: transparent;
}
.todo-item.single-line:not(.active) .todo-item--text,
.todo-add.single-line:not(.active) .todo-item--text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.todo-item.complete:not(.active) .todo-item--text,
.todo-add.complete:not(.active) .todo-item--text {
  text-decoration: line-through;
}
.todo-item.active,
.todo-add.active,
.todo-item:hover,
.todo-add:hover {
  background: #fafafa;
}
.todo-item.active .todo-item--icon.remove,
.todo-add.active .todo-item--icon.remove,
.todo-item:hover .todo-item--icon.remove,
.todo-add:hover .todo-item--icon.remove {
  color: #f44336;
}
.todo-item.active .todo-add--icon.add,
.todo-add.active .todo-add--icon.add,
.todo-item:hover .todo-add--icon.add,
.todo-add:hover .todo-add--icon.add {
  color: #2196f3;
}
.scene {
  position: relative;
}
.scene > div {
  position: relative;
  z-index: 2;
}
.scene .clone {
  position: absolute;
  z-index: 999;
  perspective: 1200px;
  perspective-origin: center center;
  -webkit-transition: 0.8s all ease;
  transition: 0.8s all ease;
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.scene .clone.animate.over .todo-box {
  -webkit-animation: zoom-in 1s 1;
  animation: zoom-in 1s 1;
}
.scene .clone.animate.under .todo-box {
  -webkit-animation: zoom-out 1s 1;
  animation: zoom-out 1s 1;
}
.scene .clone.over {
  z-index: 3;
}
.scene .clone.over .todo-box {
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.12), 0 4px 10px rgba(0, 0, 0, 0.24);
}
.scene .clone.over .todo-box .todo-tools {
  opacity: 1;
}
.scene .clone.under {
  z-index: 1;
}



